import { FormDataType } from "@/types/types";
import  emailjs, { EmailJSResponseStatus }  from '@emailjs/browser';

const SERVICE_KEY = 'service_dvyfrv7';
const TEMPLATE_KEY = 'template_dijtokg';
const PUBLIC_KEY = 'OiAYajDR_DHv1IuAj';

type EmailJSType = {
    post: (data: string | HTMLFormElement) => Promise<EmailJSResponseStatus | Error>,
}

const emailService:EmailJSType = {
    post: async (data) => {
        try {
            const responseStatus = await emailjs.sendForm(
                SERVICE_KEY,
                TEMPLATE_KEY,
                data,
                PUBLIC_KEY,
            );

            return responseStatus
        } catch (error: any) {
            return new Error(error);            
        }
    },
}

export default emailService;
